

@mixin sm {
	@media (min-width: 960px) {
		@content; } }

//	Fonts

@font-face {

	font-family: "HyundaiSansHeadRegular";
	src: url("font/HyundaiSansHeadRegular.woff2") format("woff2"), url("font/HyundaiSansHeadRegular.woff") format("woff"); }

@font-face {

	font-family: "HyundaiSansHeadLight";
	src: url("font/HyundaiSansHeadLight.woff2") format("woff2"), url("font/HyundaiSansHeadLight.woff") format("woff"); }

@font-face {

	font-family: "HyundaiSansHeadMedium";
	src: url("font/HyundaiSansHeadMedium.woff2") format("woff2"), url("font/HyundaiSansHeadMedium.woff") format("woff"); }


// Default Styles

$primary-colour: #003a6d;
$primary-colour-highlighted: #003a6d;
$primary-background-colour: #e6e6e6;
$primary-text-colour: white;
$primary-text-accented-colour: white;
$primary-text-colour-reversed: white;
$primary-font-family: "HyundaiSansHeadRegular", Arial, Helvetica, sans-serif;
$primary-font-family-light: "HyundaiSansHeadLight", Arial, Helvetica, sans-serif;
$primary-font-family-medium: "HyundaiSansHeadMedium", Arial, Helvetica, sans-serif;
$primary-font-size: 16px;
$primary-font-weight: 400;
$primary-line-height: 1.25em;

$defaultGap: 20px;
