
body {

	background-color: black;

	background-image: url("../img/bg.jpg");
	background-repeat: no-repeat;
	background-position: top center;
	background-size: 100% auto; }

.fx {

	width: 100%;
	min-height: 100vh;

	margin: auto;

	background-image: url("../img/blur.jpg");
	background-repeat: no-repeat;
	background-position: top center;
	background-size: 100% 100%;

	box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.5);

	@include sm {

		width: 940px; } }



#formScreen {

	opacity: 1;
	padding: 0;

	.block {

		padding: 0; } }

#successScreen {

	display: none;
	padding: 0;
	opacity: 0; }

.terms {

	text-align: left !important; }

.logo {

	img {

		display: block;
		margin: 20px auto;

		max-height: 30px; } }

.question {

	.block {

		text-align: center; }

	img {

		display: block;
		margin: 10px auto 60px;

		max-width: 70%; }

	span {

		font-family: $primary-font-family-light;
		font-size: 1.2em;
		line-height: 1.35em;

		&.bolder {

			font-family: $primary-font-family; } } }

form#lotty {

	width: calc(100% - 100px);
	margin: 30px auto 0;

	.block {

		text-align: center;
		padding: 10px 20px;

		label.text {

			display: block;
			width: 100%;
			padding-bottom: 10px;

			font-size: 1.1em;
			text-align: left; }

		input[type="text"] {

			display: block;
			width: 100%;

			background: #8e989c;
			border: 2px solid transparent;

			font-size: 1em;
			font-family: $primary-font-family-medium;

			line-height: 2.25em;
			padding: 2px 12px 0;

			&:focus {

				background: white;
				border: 2px solid #1b67a8; }

			&.error {

				color: #801125;
				background: #dacbcb;
				border-color: darkred; } }

		span.spin {

			display: block;
			margin-top: 40px;
			font-size: 1.3em;
			font-family: $primary-font-family-light; }

		div.spin {

			width: 200px;
			height: 80px;

			margin: 30px auto;

			cursor: pointer;

			background-image: url("../img/spinner.png");
			background-repeat: no-repeat;
			background-position: center;
			background-size: auto;

			&:active {

				background-size: 110px; } } }

	.checkbox {

		text-align: left;

		input {

			display: inline-block;
			padding-top: 2px;

			position: relative;
			top: 2px;

			cursor: pointer; }

		label {

			display: inline-block;
			line-height: 2em;
			cursor: pointer; }

		a {

			text-decoration: underline; } } }


.numbers {

	.block {

		padding: 0; }

	ul {

		margin: 0 0 50px;
		text-align: center;

		li {

			position: relative;
			display: inline-block;

			width: 40px;
			height: 66px;

			background-image: url("../img/number.png");
			background-position: center;
			background-repeat: no-repeat;

			overflow: hidden;

			@include sm {

				width: 80px;
				height: 132px; }

			&:before {

				content: "";

				position: absolute;
				left: 0;
				top: 0;

				width: 40px;
				height: 15px;

				background: linear-gradient(to bottom, rgba(0,0,0,0.85) 0%,rgba(0,0,0,0) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */;

				@include sm {

					width: 80px;
					height: 30px; } }

			&:after {

				content: "";

				position: absolute;
				left: 0;
				bottom: 0;

				width: 40px;
				height: 15px;

				background: linear-gradient(to top, rgba(0,0,0,0.85) 0%,rgba(0,0,0,0) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */;

				@include sm {

					width: 80px;
					height: 30px; } }

			.slot {

				width: 40px;
				height: 66px;

				background-image: url("../img/roll-normal.png");
				background-position-x: 0;
				background-position-y: 0px;
				background-size: 100%;
				background-repeat: repeat-y;

				@include sm {

					width: 80px;
					height: 132px; } } } } }

.disabled {

	pointer-events: none; }

footer {

	color: $primary-text-colour-reversed;
	font-family: $primary-font-family-light;

	a {

		color: $primary-text-colour-reversed;
		line-height: 34px;

		&:hover {

			color: $primary-text-colour-reversed; } }

	ul.social {

		margin: 0;
		text-align: right;

		li {

			display: inline-block;
			padding-left: 4px;

			margin: 0;

			a {

				display: block;

				width: 34px;
				height: 34px;

				img {

					display: block;
					width: 100%;
					height: 100%; } } } } }


.fadeout {

	animation: fadeout 0.75s linear 0s forwards; }

@keyframes fadeout {

	0% {

		opacity: 1; }

	100% {

		opacity: 0; } }

.fadein {

	animation: fadein 0.75s linear 0s forwards; }

@keyframes fadein {

	0% {

		opacity: 0; }

	100% {

		opacity: 1; } }
